.Container {
  width: 80%;
  height: 80%;
  display: flex;
  position: absolute;
  top: 90%;
  left: 50%;
  transform: translate(-50%, -90%);
  display: flex;
  justify-content: space-between;
  flex-flow: wrap;
}
.Item {
  width: 600px;
  height: auto;
  border: 1px solid #eee;
  box-shadow: -3px 3px 5px 5px #ccc;
  margin-top: 40px;
  padding: 20px;
  cursor: pointer;
  transition: 0.3s ease;
  overflow: hidden;
}
.Item--active {
  width: 95%;
  height: 95%;
  position: absolute;
  backdrop-filter: blur(1px);
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  background-color: lightgrey;
}
ul {
  list-style: none;
  padding: 50px;
  pointer-events: none;
}
.search--form {
  width: 500px;
  height: 150px;
  position: absolute;
  left: 50%;
  transform: translate(-50%);
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  align-items: center;
}
.search--bar {
  outline: none;
  width: 80%;
  height: 30%;
}
.search--button {
  cursor: pointer;
  width: 40%;
  height: 30%;
  font-size: 1.5rem;
  background-color: inherit;
  border: 1px solid #eee;
  transition: 0.2s ease;
  outline: none;
}
.search--button:hover {
  transform: translate(1.05);
  box-shadow: -3px 3px 5px 5px #ccc;
}
.search--button:active {
  transform: translate(0.95);
}
